.aside{width:250px;min-width:220px;height:100vh;border-right:0.0625rem solid var(--borderLine);background-color:var(--white)}.aside>.logo{height:var(--headerHeight);display:flex;align-items:center}.aside>.logo>img{margin:0 auto}.nav{width:100%}.link{padding-left:1.875rem;font-size:1rem;font-weight:500;display:flex;color:var(--greyB1);gap:1.25rem;transition:color var(--transition);height:3.125rem;align-items:center;text-decoration:none}.link:hover{color:var(--primery)}.link:not(:last-child){margin-bottom:clamp(0.625rem,0.0748239437rem + 2.3474178404vw,2.1875rem)}.isActive{position:relative;color:var(--primery)}.isActive ::before{content:"";position:absolute;width:0.3125rem;height:3.125rem;background-color:var(--primery);left:0;bottom:0;border-top-right-radius:15px;border-bottom-right-radius:15px;animation-name:before;animation-duration:.15s}@keyframes before{from{opacity:0}to{opacity:1}}@media(max-width: 1024px){.logo>img{opacity:0}.aside{display:none;background-color:var(--white);position:fixed;height:100%;top:0;left:0;bottom:0;z-index:10}}.isBurger{display:block}